.Tile {
    display: inline-block;
    margin: 0;
    border-right: 1px solid white;
    border-bottom: 1px solid white;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-transition: background-color 0.5s, -webkit-transform 0.5s linear;
    transition: background-color 0.5s, -webkit-transform 0.5s linear;
    -o-transition: background-color 0.5s, transform 0.5s linear;
    transition: background-color 0.5s, transform 0.5s linear;
    transition: background-color 0.5s, transform 0.5s linear, -webkit-transform 0.5s linear;
}

.Tile.on {
    background-color: #cccccc;
    -webkit-transform: rotateX(180deg) rotateY(180deg);
            transform: rotateX(180deg) rotateY(180deg);
}

.Tile.off {
    background-color: #222222;
}



